.socials {
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 21px 0 0;
  justify-content: center;
  border-top: 1px solid $color-grey-170;
  border-bottom: 1px solid $color-grey-170;
  margin-bottom: 21px;

  @media (min-width: $tablet-min-width) {
    justify-content: center;
    border: none;
  }
}

.socials__item {
  width: 30px;
  text-align: center;
  margin: 0 12px 16px;

  @media (min-width: $tablet-min-width) {
    margin: 0 11px;
  }
}

.socials__item--fb {
  margin-right: 15px;

  @media (min-width: $tablet-min-width) {
    margin-left: 8px;
    margin-right: 20px;
  }

  @media (min-width: $desktop-min-width) {
    margin-left: 4px;
    margin-right: 19px;
  }
}

.socials__link {
  color: $color-grey-500;

  &:hover,
  &:focus {
    color: $color-green-100;
  }

  &:active {
    opacity: 0.3;
  }
}

.socials__link svg {
  @media (min-width: $tablet-min-width) {
    transform: scale(1.39);
  }
}
